import { Component, Input, EventEmitter, Output, HostBinding } from '@angular/core';
import { PagerSettings, normalize } from './pager-settings';
import { PageChangeEvent } from './change-event-args.interface';

/**
 * @hidden
 */
const navButton = ({ buttonClass, clickExpr, disabledExpr, iconClass, message }) =>
    `<a *ngIf="settings.previousNext"
        href="#"
        title='${message}'
        (click)="${clickExpr}"
        [ngClass]="{
            'k-link': true,
            'k-pager-nav': true,
            'k-state-disabled': ${disabledExpr},
            '${buttonClass}': true
        }">
        <span aria-label="${message}"
            [ngClass]="{
                'k-icon':true,
                '${iconClass}': true
            }">
        </span>
    </a>`;

/**
 * @hidden
 */
@Component({
    selector: 'kendo-pager',
    template: `
        ${navButton({
            buttonClass: "k-pager-first",
            clickExpr: "currentPage !== 1 ? changePage(0) : false",
            disabledExpr: "prevButtonsDisabled",
            iconClass: "k-i-seek-w",
            message: "Go to the first page"
        })}
        ${navButton({
            buttonClass: "",
            clickExpr: "currentPage !== 1 ? changePage(currentPage-2) : false",
            disabledExpr: "prevButtonsDisabled",
            iconClass: "k-i-arrow-w",
            message: "Go to the previous page"
        })}
        <ul [ngClass]="{'k-pager-numbers': true, 'k-reset': true}" *ngIf="settings.type === 'numeric'">
            <li *ngIf="start > 1"><a class="k-link" href="#" (click)="changePage(start - 2)">...</a></li>
            <li *ngFor="let num of buttons">
                <a href="#"
                    [ngClass]="{'k-link': true, 'k-state-selected':currentPage == num}"
                    (click)="changePage(num - 1)">
                    {{num}}
                </a>
            </li>
            <li *ngIf="end < totalPages"><a class="k-link" href="#" (click)="changePage(end)">...</a></li>
        </ul>
        <span *ngIf="settings.type === 'input'" [ngClass]="{'k-pager-input': true, 'k-label': true}">
            Page
            <input [class.k-textbox]="true" [value]="currentPage" #inputPager (change)="onInputChange(inputPager.value)" />
            of {{totalPages}}
        </span>
        ${navButton({
            buttonClass: "",
            clickExpr: "currentPage !== totalPages ? changePage(currentPage) : false",
            disabledExpr: "nextButtonsDisabled",
            iconClass: "k-i-arrow-e",
            message: "Go to the next page"
        })}
        ${navButton({
            buttonClass: "k-pager-last",
            clickExpr: "currentPage !== totalPages ? changePage(totalPages-1) : false",
            disabledExpr: "nextButtonsDisabled",
            iconClass: "k-i-seek-e",
            message: "Go to the last page"
        })}
        <span *ngIf='settings.info' [ngClass]="{'k-pager-info': true, 'k-label': true}">
            {{(currentPage - 1) * pageSize + 1}} - {{maxItems}} of {{total}} items
        </span>
        <span *ngIf="settings.pageSizes" [ngClass]="{'k-pager-sizes': true, 'k-label': true}">
            <select #pageSizes (change)="pageSizeChange(pageSizes.value)">
                <option *ngIf="showInitialPageSize" [value]="pageSize">{{pageSize}}</option>
                <option *ngFor="let page of settings.pageSizes" [value]="page" [selected]="page === pageSize ? true : undefined">
                    {{page}}
                </option>
            </select>
            items per page
        </span>
  `
})
export class PagerComponent {
    @Input() public total: number = 0;
    @Input() public skip: number = 1;
    @Input() public pageSize: number;
    @Input() public set options(value: PagerSettings | boolean) {
        this.settings = normalize(value);
    }

    @Output() public pageChange: EventEmitter<PageChangeEvent> = new EventEmitter<PageChangeEvent>();

    @HostBinding('class.k-pager-wrap') get pagerWrapClass(): boolean {
        return true;
    }
    @HostBinding('class.k-grid-pager') get gridPagerClass(): boolean {
        return true;
    }
    @HostBinding('class.k-widget') get widgetClass(): boolean {
        return true;
    }

    public settings: PagerSettings = normalize({});

    public get prevButtonsDisabled(): boolean {
        return this.currentPage === 1;
    }

    public get nextButtonsDisabled(): boolean {
        return this.currentPage === this.totalPages;
    }

    public get showInitialPageSize(): boolean {
        return (<Array<number>>this.settings.pageSizes)
            .filter(num => num === Number(this.pageSize))
            .length === 0;
    }

    public pageSizeChange(value: any): void {
        this.pageSize = value;
        this.changePage(0);
    }

    public changePage(page: number): boolean {
        this.pageChange.emit({ skip: page * this.pageSize, take: this.pageSize });
        return false;
    }

    public get currentPage(): number {
        return Math.floor((this.skip || 0) / this.pageSize) + 1;
    }

    public get totalPages(): number {
        return Math.ceil((this.total || 0) / this.pageSize);
    }

    public onInputChange(value: string): void {
        let page = parseInt(value, 10);
        if (isNaN(page) || page < 1 || page > this.totalPages) {
            page = this.currentPage;
        }
        this.changePage(page - 1);
    }

    public get buttons(): number[] {
        let result: any[] = [];

        for (let idx: number = this.start; idx <= this.end; idx++) {
            result.push(idx);
        }

        return result;
    }

    private get end(): number {
        return Math.min((this.start + this.settings.buttonCount) - 1, this.totalPages);
    }

    private get maxItems(): number {
        return Math.min(this.currentPage * this.pageSize, this.total);
    }

    private get start(): number {
        const page: number = this.currentPage;
        const buttonCount: number = this.settings.buttonCount;

        if (page > buttonCount) {
            const reminder: number = (page % buttonCount);

            return (reminder === 0) ? (page - buttonCount) + 1 : (page - reminder) + 1;
        }

        return 1;
    }
}



